import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './index/index.component';
import { AccountsettingsComponent } from './accountsettings/accountsettings.component';
import { AdPostComponent } from './ad-post/ad-post.component';
import { AdsearchComponent } from './adsearch/adsearch.component';
import { AdverificationComponent } from './adverification/adverification.component';
import { CategoriesComponent } from './categories/categories.component';
import { ChangepasswordComponent } from './changepassword/changepassword.component';
import { ForgetpasswordComponent } from './forgetpassword/forgetpassword.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { SubmitAdComponent } from './submit-ad/submit-ad.component';
import { SubmitAdJobComponent } from './submit-ad-job/submit-ad-job.component';

const routes: Routes = [
  {path:'',component:IndexComponent},
  {path:'accountsettings',component:AccountsettingsComponent},
  {path:'ad-post',component:AdPostComponent},
  {path:'adsearch',component:AdsearchComponent},
  {path:'adverification',component:AdverificationComponent},
  {path:'categories',component:CategoriesComponent},
  {path:'changepassword',component:ChangepasswordComponent},
  {path:'forgetpassword',component:ForgetpasswordComponent},
  {path:'login',component:LoginComponent},
  {path:'register',component:RegisterComponent},
  {path:'submit-ad',component:SubmitAdComponent},
  {path:'submit-ad-job',component:SubmitAdJobComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
